"use strict";
import React,{Component, PropTypes} from 'react'
import IsPcOrNot from '../../../components/lib/IsPcOrNot.js'
import en from '../../../locale/en';
import zh from '../../../locale/zh';
import Header from '../../../components/Header/Header'
import Footer from '../../../components/Footer/Footer'
import Constant from '../../../components/lib/Constant'
import $ from 'n-zepto'
import Params from '../../../components/lib/QueryParams'
import {FormattedMessage} from '../../../components/lib/BaseComponent'
import pc_bg from '../assets/pc_bg.png'
import phone_bg from '../assets/phone_bg.png'



let messages = {};
messages['en'] = en;
messages['zh-CN'] = zh;
messages['zh-cn'] = zh;

class Phone extends Component {
    constructor(props) {
        super(props);
        this.qrcode = {
            url : ''
        };

    }
    componentDidMount() {
        $.get(Constant.url + "qrcode.do?c="+Params('c'),(data)=>{
            this.qrcode.url = data.data;
            this.setState(this.qrcode);
        })
    }
    render() {
        return <div style={{backgroundColor:"#fff"}}>
            <div style={{display:"none"}}>
                <Header/>
            </div>
            <div style={{
                backgroundImage:"url("+phone_bg+")",
                backgroundPosition:"top left",
                backgroundSize:"100% auto",
                backgroundRepeat:"no-repeat",
                height:"12.5rem"

            }}>
                <img style={{
                    width:"3.6rem",
                    height:"3.6rem",
                    position:"absolute",
                    left:"50%",
                    marginLeft:"-1.8rem",
                    top:"6.08rem"
                }} src={"data:image/png;base64,"+this.qrcode.url}/>
            </div>
            <Footer/>
        </div>
    }

}
class PC extends Component {
    constructor(props) {
        super(props);
        this.qrcode = {
            url : ''
        };

    }
    componentDidMount() {
        $.get(Constant.url + "qrcode.do?c="+Params('c'),(data)=>{
            this.qrcode.url = data.data;
            this.setState(this.qrcode);
        })
    }
    render() {
        return <div style={{minHeight:"100%",backgroundColor:"#fff"}}>
            <div style={{display:"none"}}>
                <Header/>
            </div>
            <div style={{
                backgroundImage:"url("+pc_bg+")",
                backgroundPosition:"top left",
                backgroundSize:"100% auto",
                backgroundRepeat:"no-repeat",
                height:$(document).height()

            }}>
                <img style={{
                    width:"19%",
                    height:"30%",
                    position:"absolute",
                    right:"14%",
                    top:"24%"
                }} src={"data:image/png;base64,"+this.qrcode.url}/>
            </div>
        </div>
    }

}
export class Qrcode extends Component {
    constructor(props) {
        super(props);
    }
    componentDidMount() {
    }
    render() {
        return <div>{
            IsPcOrNot.get()?<PC/>:<Phone/>
        }
        </div>
    }
}
export default Qrcode;


